
.el-checkbox__input.is-disabled.is-checked .el-checkbox__inner {
  background-color: RGBA(0, 155, 27, 0.8);
  border-color: RGBA(0, 155, 27, 0.8);
}
.el-checkbox__input.is-disabled + span.el-checkbox__label {
  color: #777;
}
.fyzl-item {
  display: inline-block;
  width: 49%;
  font-size: 14px;
}
.fyzl-item .fyzl-item-td {
    display: inline-block;
    margin-left: 10px;
}
#input1 {
  font-size: 18px;
  width: 90%;
  padding: 8px 18px;
}
#input1:focus {
  outline: #00A61B auto 2px;
  border: 1px solid #00A61B;
  border-radius: 3px;
}
.loading .el-loading-spinner .el-loading-text {
  font-size: 25px;
  color: #fff;
}
.search-box {
  width: 720px;
  margin: auto;
}
.search-box .title {
    /*margin-top: 70px;*/
    font-size: 36px;
    color: #199025;
    text-align: center;
}
.search-box .search {
    margin-top: 30px;
}
.search-box .search .el-input {
      font-size: 18px;
      padding: 5px 0;
}
.search-box .search #clbh .el-input {
      width: 350px;
      margin-left: 50px;
}
.search-box .search #time .el-input {
      width: 150px;
}
.search-box .search .el-button {
      font-size: 15px;
      /*float:left;*/
}
.search-box .el-table td, .search-box .el-table th {
    padding: 9px 0;
}
.search-box .tips {
    margin-top: 5px;
    color: #ff544d;
    font-size: 18px;
    padding-left: 25px;
}
.btn {
  text-align: center;
  margin-top: 15px;
}
.account-form .account-title {
  /*text-align: center;*/
  padding-bottom: 7px;
  font-size: 28px;
  margin-bottom: 40px;
  border-bottom: 1px solid #ccc;
  color: #696969;
}
.account-form .account-title .company {
    margin-left: 60px;
}
.account-form .account-title .number {
    color: #0B911B;
    font-weight: bold;
}
.account-form .el-form-item__label {
  font-size: 16px;
}
.account-form .el-form-item {
  width: 49%;
  float: left;
  margin-bottom: 18px;
}
.account-form .el-radio {
  margin: 10px 40px;
}
.account-form .el-radio .el-radio__inner {
    width: 20px;
    height: 20px;
}
.account-form .el-input__inner {
  color: #707276;
  font-weight: normal;
}
.account-form .txt {
  width: 98%;
}
.account-form .txt .el-textarea__inner {
    font-size: 16px;
    height: 80px;
    resize: none !important;
}
.print .print-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}



